import React, { Component } from 'react';
import Link from 'gatsby-link';
import './style.css';

const blogs = [
  {
    title: 'Hello World',
    slug: '/foo/',
  },
  {
    title: 'Hello Man',
    slug: '/bar/',
  },
];

const Sidebar = () => (
  <div className="sidebar">
    <header>热门推荐</header>
    {blogs.map(x => (
      <header key={x.slug}>
        <Link to={x.slug}>{x.title}</Link>
      </header>
    ))}
  </div>
);

export default Sidebar;



