import React from 'react';
import Sidebar from '../Sidebar';
import './style.css';

const NavMenu = ({ showNavMenu = false }) => {
  if (!showNavMenu) return false;
  return (
    <div className="nav-menu">
      <Sidebar />
    </div>
  );
};

export default NavMenu;



